  width: 40px;
  height: 40px;
  justify-self: end;
  background-color: #316dc2;
  border-radius: 50%;
}

.faq__qustions-button::before,
.faq__qustions-button::after {
  content: "";
  position: absolute;
  width: 18px;
  height: 1px;
  background-color: #ffffff;
  top: 50%;
  left: 10px;
}

